
import * as pixi from 'pixi.js'
import {Actor} from './actor'


function loadAssets() {
  return new Promise((resolve, reject) => {
    let sprites:any = {};
    pixi.loader
      .add('sampleactor','assets/sampleactor.json')
      .load((loader:pixi.loaders.Loader, resources:any) => {
        loader;
        let framenames = resources.sampleactor.data.frames;
        let actions:any = {};
        for(let framename in framenames) {
          let regex = /([a-zA-Z_]+)(\d*)/;
          if(regex.test(framename)) {
            let match = regex.exec(framename);
            let action = match![1];
            let index = parseInt(match![2]);
            if(actions[action]) {
              actions[action].push(index);
            } else {
              if(isNaN(index)) {
                actions[action] = [];
              } else {
                actions[action] = [index];
              }
            }
          }
        }
        for(let actionName in actions) {
          let frameIndices = actions[actionName];
          if(frameIndices.length > 0) {
            frameIndices.sort((a:number,b:number) => a-b);
            let frames = [];
            for(let idx of frameIndices) {
              frames.push(pixi.Texture.fromFrame(actionName+idx+'.png'));
            }
            let sprite = new pixi.extras.AnimatedSprite(frames);
            sprites[actionName] = sprite;

          } else {
            let texture = pixi.Texture.fromFrame(actionName+'.png');
            let sprite = new pixi.Sprite(texture);
            sprites[actionName] = sprite;
          }
        }

        resolve(sprites);
      })
      .onError.add((e:any) => {
        reject(e);
      })
      ;
  });
}

export function runActorStudio() {
  let W = window.innerWidth;
  let H = window.innerHeight;
  let actor:Actor|undefined;

  let app = new pixi.Application(W,H, { backgroundColor: 0xffffff });
  document.body.appendChild(app.view);
  app.view.style.position = 'absolute';
  app.view.style.left = '0px';
  app.view.style.top = '0px';

  let controlDiv = document.createElement('div');
  controlDiv.style.position = 'absolute';
  controlDiv.style.right = '0px';
  controlDiv.style.bottom = '0px';
  controlDiv.style.padding = '20px';
  controlDiv.innerHTML = `
  Anim Speed
  <input type="range" name="animspeed"
    min="0.1" max="2.0" value="0.2" step="0.1"/>
  Movement Speed
  <input type="range" name="movespeed"
    min="0.1" max="5.0" value="1" step="0.1"/>
  `;
  let animSpeedInput = <HTMLInputElement>controlDiv.querySelector(
      'input[name=animspeed]');
  let moveSpeedInput = <HTMLInputElement>controlDiv.querySelector(
      'input[name=movespeed]');
  document.body.appendChild(controlDiv);

  animSpeedInput.addEventListener('change', () => {
    actor!.setMovementAnimSpeed(parseFloat(animSpeedInput.value));
  });
  moveSpeedInput.addEventListener('change', () => {
    actor!.speed = parseFloat(moveSpeedInput.value);
  });

  app.view.addEventListener('mouseup', (ev:MouseEvent) => {
    if(actor) {
      actor.moveAlongPath([[ev.clientX,ev.clientY]]);
    }
  });

  // Prevent right-click context menu
  app.view.addEventListener('contextmenu', (ev) => {
    ev.preventDefault();
  });

  loadAssets().then((sprites:any) => {
    sprites.move.play();
    let center = [W/2,H/2];
    actor = new Actor(sprites.rest, sprites.move, center);
    actor.setMovementAnimSpeed(parseFloat(animSpeedInput.value));
    actor.speed = parseFloat(moveSpeedInput.value);
    for(let sprite of actor.allSprites) {
      app.stage.addChild(sprite);
    }
  });

  app.ticker.add((dt:number) =>  {
    if(actor && actor.inmotion) {
      actor.move(dt);
    }
  });
}


