
import * as pixi from 'pixi.js'

function dot(a:number[],b:number[]) {
  return a[0]*b[0]+a[1]*b[1];
}

/**
 * Z-coordinate of cross product (because x and y are zero)
 */
function cross(a:number[],b:number[]) {
  return a[0] * b[1] - a[1] * b[0];
}

export class Actor {

  private _restSprite : pixi.Sprite;
  private _motionSprite : pixi.extras.AnimatedSprite;

  private _allSprites : pixi.Sprite[];
  private _activeSprite : pixi.Sprite;

  private _position : number[];

  inmotion : boolean;
  private _target : number[]|null;
  private _path : number[][]|null;
  private _lastdir : number[]|null;
  speed : number;

  constructor(
    restSprite:pixi.Sprite,
    motionSprite:pixi.extras.AnimatedSprite,
    position=[0,0])
  {
    this._restSprite = restSprite;
    this._motionSprite = motionSprite;
    this._allSprites = [this._restSprite,this._motionSprite];

    this.setActiveSprite(this._restSprite);

    this.speed = 1;
    this.inmotion = false;
    this._target = null;
    this._path = null;
    this.position = position;

    for(let sprite of this._allSprites) {
      sprite.anchor.set(0.5);
      sprite.scale.x = 0.25;
      sprite.scale.y = 0.25;
    }
  }

  private setActiveSprite(sprite:pixi.Sprite) {
    for(let sp of this._allSprites) { sp.visible = false; }
    this._activeSprite = sprite;
    sprite.visible = true;
  }

  get activeSprite() : pixi.Sprite {
    return this._activeSprite;
  }

  get allSprites() : pixi.Sprite[] {
    return this._allSprites;
  }

  set position(pos:number[]) {
    this._position = pos;
    for(let sprite of this._allSprites) {
      sprite.x = pos[0];
      sprite.y = pos[1];
    }
  }
  set rotation(angle:number) {
    for(let sprite of this._allSprites) {
      sprite.rotation = angle;
    }
  }

  get position() {
    return this._position;
  }

  setMovementAnimSpeed(value:number) {
    this._motionSprite.animationSpeed = value;
  }

  moveTo(x:number,y:number) {
    this.inmotion = true;
    this._target = [x,y];
    this._lastdir = null;
    this.setActiveSprite(this._motionSprite);
  }

  moveAlongPath(path:number[][]) {
    this.inmotion = true;
    this._path = path;
    this._lastdir = null;
    this.setActiveSprite(this._motionSprite);
  }

  get target() {
    return this._target;
  }

  move(dt:number) {
    dt;
    if(this._target) {
      let vec = [this._target[0]-this._restSprite.position.x,
                this._target[1]-this._restSprite.position.y]
      let len = Math.sqrt(vec[0]*vec[0]+vec[1]*vec[1]);
      if(len < this.speed/2) {
        this.inmotion = false;
        this._target = null;
        this.setActiveSprite(this._restSprite);
      } else {
        let dir = [vec[0]/len,vec[1]/len];
        this.position = [
          this._restSprite.position.x+this.speed*dir[0],
          this._restSprite.position.y+this.speed*dir[1]
        ];
      }
    } else if(this._path && this._path.length > 0) {
      let [tx,ty] = this._path[0];
      let {x,y} = this._restSprite.position;
      let vec = [tx-x,ty-y];
      let len = Math.sqrt(vec[0]*vec[0]+vec[1]*vec[1]);
      let dir = [vec[0]/len,vec[1]/len];

      // No rotation = pointing up
      let sense = cross(dir, [0,-1]) >= 0 ? -1 : 1;
      let angle = Math.acos(dot(dir,[0,-1])); 
      this.rotation = sense * angle;

      let crossedover = false;
      if(this._lastdir !== null) {
        crossedover = dot(dir,this._lastdir) < 0;
      }
      if(len < 1 || crossedover) {
        // switch to next path, if we are not at end of it yet
        if(this._path.length > 1) {
          this._path.shift();
        } else {
          this.inmotion = false;
          this._path = null;
          this.setActiveSprite(this._restSprite);
        }
        if(crossedover) {
          this.position = [tx,ty];
        }
      } else {
        this.position = [
          this._restSprite.position.x+this.speed*dir[0],
          this._restSprite.position.y+this.speed*dir[1]
        ];
      }
      this._lastdir = dir;

    } else {
      this.inmotion = false;
      this._path = null;
      this._target = null;
      this.setActiveSprite(this._restSprite);
    }
  }
}

export class Hero extends Actor {

  constructor(
    restSprite:pixi.Sprite,
    motionSprite:pixi.extras.AnimatedSprite,
    location=[0,0])
  {
    super(restSprite, motionSprite, location);
  }
}


