
import * as pixi from 'pixi.js'
import {basic_hex} from '../assets/gamedata'

const size = {x:20,y:20};
const ORIGIN = [50,50];

interface HexTransform {
  f0 : number;
  f1 : number;
  f2 : number;
  f3 : number;
  b0 : number;
  b1 : number;
  b2 : number;
  b3 : number;
  start_angle : number; // fraction of PI/6
  origin : number[];
}

class Hex {

  public static PointyTransform:HexTransform;

  public static directions:number[][] = [
    [1, 0, -1],
    [1, -1, 0],
    [0, -1, 1],
    [-1, 0, 1],
    [-1, 1, 0],
    [0, 1, -1]
  ];

  static add(a:number[],b:number[]) {
    return [a[0]+b[0],a[1]+b[1],a[2]+b[2]];
  }

  static sub(a:number[],b:number[]) {
    return [a[0]-b[0],a[1]-b[1],a[2]-b[2]];
  }

  static scale(a:number[], k:number) {
    return [a[0]*k, a[1]*k, a[2]*k];
  }

  static distanceManhattan(a:number[], b:number[]) {
    let [dx,dy,dz] = Hex.sub(b,a);
    return Math.abs(dx)+Math.abs(dy)+Math.abs(dz);
  }

  static neighbor(a:number[], direction:number) {
    return Hex.add(a,Hex.directions[direction]);
  }

  static screenToHexCoord(coord:number[], M:HexTransform) {
    let x = (coord[0]-M.origin[0])/size.x;
    let y = (coord[1]-M.origin[1])/size.y;
    let q = Math.round(M.b0 * x + M.b1 * y);
    let r = Math.round(M.b2 * x + M.b3 * y);
    return [q,-q-r,r];
  }

  // Give center of the HexCell in Screen coordinates
  static hexCoordToScreen(hex:number[], M:HexTransform) {
    let q = hex[0];
    let r = hex[2];
    let x = (M.f0 * q + M.f1 * r) * size.x;
    let y = (M.f2 * q + M.f3 * r) * size.y;
    return [M.origin[0]+x,M.origin[1]+y];
  }

  static gridToPolygon(hex:number[], M:HexTransform) {
    let center = Hex.hexCoordToScreen(hex,M);
    let polygon = [];
    for(let corner=0; corner<6; corner++) {
      var angle = 2.0 * Math.PI * (M.start_angle - corner) / 6;
      polygon.push([
        center[0] + size.x * Math.cos(angle),
        center[1] + size.y * Math.sin(angle)
      ]);
    }
    return polygon;
  }
}

Hex.PointyTransform = {
  f0 : Math.sqrt(3.0),
  f1 : Math.sqrt(3.0) / 2.0,
  f2 : 0.0,
  f3 : 3.0 / 2.0,
  b0 : Math.sqrt(3.0) / 3.0,
  b1 : -1.0 / 3.0,
  b2 : 0.0,
  b3 : 2.0 / 3.0,
  start_angle : 0.5,
  origin : ORIGIN
};

class Cell {
  g : number;
  h : number;
  predecessor : Cell|null;

  constructor(public coord:number[]) {
    this.g = 0;
    this.h = 0;
    this.predecessor = null;
  }

  getCenterScreenCoord() : number[] {
    throw new Error('Not implemented');
  }
}

class HexCell extends Cell {
  constructor(hexcoord:number[]) {
    super(hexcoord);
  }

  getCenterScreenCoord() {
    return Hex.hexCoordToScreen(this.coord, Hex.PointyTransform);
  }
}

interface GridSpec {
  type : string;
  basegrid : number[][];
}

export class TerrainGrid {

  public cellgrid : Cell[][];
  public wallgrid : number[][];

  constructor(gridspec:GridSpec) {
    this.wallgrid = gridspec.basegrid;
    let nrows = this.wallgrid.length;
    let ncols = this.wallgrid[0].length;

    // Populate Hex Cells in the rectangular grid
    let cell = new HexCell([0,0,0]);
    let grid : Cell[][] = [];
    for(let i=0; i<nrows; i++) {
      grid[i] = [];
      let row = [];
      for(let j=0; j<ncols; j++) {
        row.push(cell);
        let neighbor = Hex.neighbor(cell.coord,1);
        cell = new HexCell(neighbor);
      }
      grid[i] = row;
      if(i%2) {
        cell = new HexCell(Hex.neighbor(row[0].coord, 3));
      } else {
        cell = new HexCell(Hex.neighbor(row[0].coord, 2));
      }
    }
    this.cellgrid = grid;
  }

  heuristicManhattan(fromNode:Cell, toNode:Cell) {
    return Hex.distanceManhattan(fromNode.coord, toNode.coord);
  }

  heuristicDiagonal(fromNode:Cell, toNode:Cell) {
    let [dx,dy] = Hex.sub(toNode.coord,fromNode.coord);
    dx = Math.abs(dx);
    dy = Math.abs(dy);
    return (dx+dy) - Math.min(dx,dy);
  }

  heuristicEuclidean(fromNode:Cell, toNode:Cell) {
    let [dx,dy] = Hex.sub(toNode.coord,fromNode.coord);
    return Math.sqrt(dx*dx+dy*dy);
  }

  heuristic(fromNode:Cell, toNode:Cell) {
    // return this.heuristicDiagonal(fromNode, toNode);
    return this.heuristicManhattan(fromNode, toNode);
    // return this.heuristicEuclidean(fromNode, toNode);
  }

  private qrTOxy(q:number,r:number) {
    return[q+(r>>1),r];
  }

  getCellAtScreenCoord(coord:number[]) {
    let hexcoord = Hex.screenToHexCoord(coord,Hex.PointyTransform);
    return this.getCellAtHexCoord(hexcoord);
  }

  hexCoordToGridLocation(hexcoord:number[]) {
    let q = hexcoord[0]; // x
    let r = hexcoord[2]; // z
    let [col,row] = this.qrTOxy(q,r);
    let nrows = this.cellgrid.length;
    let ncols = this.cellgrid[0].length;
    if(col >= 0 && col < ncols && row >= 0 && row < nrows) {
      return [row,col];
    } else {
      return null;
    }
  }

  getCellAtRowCol(row:number,col:number) {
    let nrows = this.cellgrid.length;
    let ncols = this.cellgrid[0].length;
    if(col >= 0 && col < ncols && row >= 0 && row < nrows) {
      return this.cellgrid[row][col];
    } else {
      return null;
    }
  }

  getCellAtHexCoord(hexcoord:number[]) {
    let gridloc = this.hexCoordToGridLocation(hexcoord);
    if(gridloc) {
      let [row,col] = gridloc;
      return this.cellgrid[row][col];
    } else {
      return null;
    }
  }

  getTiles() {
    let tiles:{type:number,polygon:number[][]}[] = [];
    this.forEachNode( (node:Cell) => {
      let gridloc = this.hexCoordToGridLocation(node.coord);
      let [row,col] = gridloc!;
      tiles.push({
        type : this.wallgrid[row][col],
        polygon : Hex.gridToPolygon(node.coord,Hex.PointyTransform)
      });
    });
    return tiles;
  }

  getHexNeighbors(hexcoord:number[]) {
    let gridloc = this.hexCoordToGridLocation(hexcoord);
    if(!gridloc) {
      return [];
    }
    let neighbors = [];
    let nrows = this.cellgrid.length;
    let ncols = this.cellgrid[0].length;
    let [row,col] = gridloc;

    if(row%2) { // odd 
      if(row > 0) {
        neighbors.push([row-1,col]);
        if(col < ncols-1) {
          neighbors.push([row-1,col+1]);
        }
      }
      if(col > 0) {
        neighbors.push([row,col-1]);
      }
      if(col < ncols-1) {
        neighbors.push([row,col+1]);
      }
      if(row < nrows-1) {
        neighbors.push([row+1,col]);
        if(col < ncols-1) {
          neighbors.push([row+1,col+1]);
        }
      }
    } else { // even 
      if(row > 0) {
        if(col > 0) {
          neighbors.push([row-1,col-1]);
        }
        neighbors.push([row-1,col]);
      }
      if(col > 0) {
        neighbors.push([row,col-1]);
      }
      if(col < ncols-1) {
        neighbors.push([row,col+1]);
      }
      if(row < nrows-1) {
        if(col > 0) {
          neighbors.push([row+1,col-1]);
        }
        neighbors.push([row+1,col]);
      }
    }
    return neighbors.map(([row,col]) => this.cellgrid[row][col]);
  }

  forEachNode(callback:(node:Cell,row:number,col:number)=>void) {
    let nrows = this.cellgrid.length;
    let ncols = this.cellgrid[0].length;
    for(let row=0; row<nrows; row++) {
      for(let col=0; col<ncols; col++) {
        callback(this.cellgrid[row][col],row,col);
      }
    }
  }

  /**
   * A* OR Dijkstra search path algorithm
   */
  searchPath(start:Cell, goal:Cell, algo='a*') {

    let openSet:Cell[] = [];
    let closedSet:Cell[] = [];

    if(algo === 'dijkstra') {
      this.forEachNode(node => {
        node.g = Infinity;
        node.h = 0;
        node.predecessor = null; 
        openSet.push(node);
      });

    } else if(algo === 'a*') { // A*
      this.forEachNode(node => {
        node.g = Infinity;
        node.h = 0;
        node.predecessor = null; 
      });
      openSet.push(start);
    } else {
      console.assert(false);
    }

    function lowestFScoreNode() {
      let idx = -1;
      let lowest = Infinity;
      for(let i=0; i<openSet.length; i++) {
        let node = openSet[i];
        let f = node.g + node.h;
        if(f < lowest) {
          lowest = f;
          idx = i;
        }
      }
      if(idx < 0) {
        // All costs are infinity, pick first one arbitrarily
        idx = 0;
      }
      return openSet.splice(idx,1)[0];
    }

    function reconstructPath(endnode:Cell) {
      let path = [endnode];
      let node = endnode;
      while(node.predecessor) {
        node = node.predecessor;
        path.unshift(node);
      }
      return path;
    }

    start.g = 0;
    start.h = this.heuristic(start, goal);

    while(openSet.length > 0) {
      let current:Cell = lowestFScoreNode();
      if(current === goal) {
        return reconstructPath(goal);
      }

      closedSet.push(current);

      let neighbors = this.getHexNeighbors(current.coord);
      for(let neighbor of neighbors) {

        let gridloc = this.hexCoordToGridLocation(neighbor.coord);
        console.assert(gridloc !== null);
        let [row,col] = gridloc!;
        let iswall = (this.wallgrid[row][col] === 0);

        if(iswall) {
          continue;
        }

        // If neighbor is in closedSet, it's already evaluated. Ignore it
        if(closedSet.indexOf(neighbor) >= 0) {
          continue;
        }
        // If neighbor is not in openSet, add it to it
        if(openSet.indexOf(neighbor) < 0) {
          openSet.push(neighbor);
        }

        // Distance from start to the neighbor
        let g = current.g +
          Hex.distanceManhattan(current.coord,neighbor.coord);

        if(g > neighbor.g) {
          continue; // this is not a better path
        }

        neighbor.predecessor = current;
        neighbor.g = g;
        if(algo === 'dijkstra') {
          neighbor.h = 0;
        } else {
          neighbor.h = this.heuristic(neighbor,goal);
        }
      }
    }
    return [];
  }

  toSVG() {
    let paths:string[] = [];
    this.forEachNode((node:Cell,row:number,col:number) => {
      let polygon = Hex.gridToPolygon(node.coord, Hex.PointyTransform);
      let color = (col%2 === 0) ?
        (row%2 === 0) ? '#bbb' : '#aaa'
        :
        (row%2 === 0) ? '#ccc' : '#999' ;
      let style = `stroke:none;fill:${color}`;
      paths.push(
        `<polygon points="${polygon.map(point => ''+point).join(' ')}"`+
        ` style="${style}"></polygon>`);
    });
    let labels:string[] = [];
    let nrows = this.wallgrid.length;
    let ncols = this.wallgrid[0].length;
    for(let i=0; i<nrows; i++) {
      let x = 10;
      let y = Hex.PointyTransform.origin[1] + i*(3/2)*size.y + size.y/2;
      labels.push(`<text x="${x}" y="${y}"><tspan x="${x}" y="${y}">`+
        `${i}</tspan></text>`);
    }
    for(let i=0; i<ncols; i++) {
      let x = Hex.PointyTransform.origin[0] + i*Math.sqrt(3)*size.x;
      let y = 15;
      labels.push(`<text x="${x}" y="${y}"><tspan x="${x}" y="${y}">`+
        `${i}</tspan></text>`);
    }

    return `<g>${paths.join('\n')} \n ${labels.join('\n')}</g>`;
  }

  toString() {
    return this.cellgrid.map(
      row => row.map(cell => '['+cell.coord[0]+','+cell.coord[2]+']').join(','))
      .join('\n');
  }
}

export function runHexDev() {

  let W = window.innerWidth;
  let H = window.innerHeight;

  // let svg = document.createElementNS('http://www.w3.org/2000/svg','svg');
  // svg.setAttribute('width',W+'px');
  // svg.setAttribute('height',H+'px');
  // document.body.appendChild(svg);
  // svg.innerHTML = hexgrid.toSVG();

  let app = new pixi.Application(W,H, { backgroundColor: 0xdddddd });
  document.body.appendChild(app.view);
  app.view.style.position = 'absolute';
  app.view.style.left = '0px';
  app.view.style.top = '0px';

  let baseGraphics = new pixi.Graphics();
  let variableGraphics = new pixi.Graphics();

  function drawPolygon(polygon:number[][],color:number,gr:pixi.Graphics) {
    gr.beginFill(color, 1);
    gr.lineStyle(2, 0x444444, 1);
    let firstPoint = polygon[0];
    gr.moveTo(firstPoint[0],firstPoint[1]);
    for(let i=1; i<polygon.length; i++) {
      let point = polygon[i];
      gr.lineTo(point[0],point[1]);
    }
    gr.lineTo(firstPoint[0],firstPoint[1]);
    gr.endFill();
  }

  function drawPolyline(polyline:number[][], color=0x444444, gr:pixi.Graphics) {
    if(polyline.length <= 0) { return; }
    gr.lineStyle(2, color, 1);
    let firstPoint = polyline[0];
    gr.moveTo(firstPoint[0],firstPoint[1]);
    for(let i=1; i<polyline.length; i++) {
      let point = polyline[i];
      gr.lineTo(point[0],point[1]);
    }
  }

  function drawHexGrid() {
    for(let i=0; i<hexgrid.cellgrid.length; i++) {
      let row = hexgrid.cellgrid[i];
      for(let j=0; j<row.length; j++) {
        let polygon = Hex.gridToPolygon(row[j].coord,Hex.PointyTransform);
        drawPolygon(
          polygon, hexgrid.wallgrid[i][j] ? 0xffff88 : 0x888888, baseGraphics);
      }
    }
  }

  let hexgrid = new TerrainGrid(basic_hex);
  let svgdoc = `
  <svg
    xmlns:dc="http://purl.org/dc/elements/1.1/"
    xmlns:cc="http://creativecommons.org/ns#"
    xmlns:rdf="http://www.w3.org/1999/02/22-rdf-syntax-ns#"
    xmlns:svg="http://www.w3.org/2000/svg"
    xmlns="http://www.w3.org/2000/svg"
    version="1.1"
    width="${W}" height="${H}">

  ${hexgrid.toSVG()}

  </svg>`.replace('\n',' ');

  let link = document.createElement('a');
  link.setAttribute('href',`data:image/svg+xml;utf8,${svgdoc}`)
  link.innerText = "Download SVG";
  document.body.appendChild(link)
  drawHexGrid();

  let startNode = hexgrid.getCellAtRowCol(4,1);
  let endNode = null;

  app.view.addEventListener('mousemove', (ev:MouseEvent) => {

    variableGraphics.clear();

    let hexcoord = Hex.screenToHexCoord(
      [ev.clientX,ev.clientY],Hex.PointyTransform);
    endNode = hexgrid.getCellAtHexCoord(hexcoord);

    if(endNode) {
      let path = hexgrid.searchPath(startNode!, endNode!);
      let polyline : number[][] = [];
      path.forEach((node:Cell) => {
        drawPolygon(Hex.gridToPolygon(
          node.coord, Hex.PointyTransform),0xee78ee, variableGraphics);
        let center = Hex.hexCoordToScreen(node.coord,Hex.PointyTransform);
        polyline.push(center);
      });

      drawPolyline(polyline, 0x000000, variableGraphics);
    }

  });

  app.view.addEventListener('click', (ev:MouseEvent) => {
    let hexcoord = Hex.screenToHexCoord(
      [ev.clientX,ev.clientY],Hex.PointyTransform);
    startNode = hexgrid.getCellAtHexCoord(hexcoord);
  });

  app.stage.addChild(baseGraphics);
  app.stage.addChild(variableGraphics);
}


