
/// <reference path="../javascript-astar.d.ts"/>
import {astar,Graph} from 'javascript-astar'

import {basic_rect} from '../assets/gamedata'

export class Terrain {
  private grid : number[][];
  private graph : Graph;
  worldW :number;
  worldH :number;
  stepX : number;
  stepY : number;

  constructor(worldW:number,worldH:number) {
    this.grid = basic_rect.basegrid;
    this.worldW = worldW;
    this.worldH = worldH;
    let ncellsY = this.grid.length;
    let ncellsX = this.grid[0].length;
    this.stepY = this.worldH/ncellsY;
    this.stepX = this.worldW/ncellsX;

    this.graph = new Graph(this.grid,{diagonal:true});
  }

  toGridLocation(worldX:number,worldY:number) {
    return [
      Math.floor(worldY/this.stepY),
      Math.floor(worldX/this.stepX)
    ];
  }

  fromGridLocation(xidx:number,yidx:number) {
    let sx = this.stepY;
    let sy = this.stepX;
    return [
      yidx*sy + sy/2,
      xidx*sx + sx/2
    ];
  }

  searchPath(from:number[],to:number[]) {
    let [fx,fy] = from;
    let [tx,ty] = to;
    let t0 = performance ? performance.now() : new Date().getTime();
    let result = astar.search(this.graph,
      this.graph.grid[fx][fy],this.graph.grid[tx][ty], {closest:true});
    let t1 = performance ? performance.now() : new Date().getTime();
    if((t1-t0) > 5) {
      console.warn('Took longer to search',(t1-t0).toFixed(2),'msec');
    }
    let thisref = this;
    return result.map( (gridnode:GridNode) => {
      return thisref.fromGridLocation(gridnode.x,gridnode.y);
    });
  }

  getTiles() {
    let tiles = [];
    let ncellsX = this.grid[0].length;
    for(let yidx=0; yidx<this.grid.length; yidx++) {
      let cellsX = this.grid[yidx];
      for(let xidx=0; xidx<ncellsX; xidx++) {
        let cell = cellsX[xidx];
        tiles.push({
          type : cell,
          dim : [
            xidx * this.stepX,
            yidx * this.stepY,
            this.stepX,
            this.stepY
          ]
        });
      }
    }
    return tiles;
  }
}



