

import {runGame} from './game'
import {runHexDev} from './terraingrid'
import {runTriDev} from './triterrain'
import {runActorStudio} from './actorstudio'

import {editTerrainWeights} from './editor'
import {playGame} from './game'

function parseTaskName() {
  let urlmatch = /#([\d\w-]+)$/.exec(window.location.href);
  if(urlmatch) {
    return urlmatch[1];
  } else {
    return 'game';
  }
}

window.onload = function () {

  let taskname = parseTaskName();
  if(taskname === 'game') {
    runGame("hex");
  } else if(taskname === 'rectgame') {
    runGame("rect");
  } else if(taskname === 'trigame') {
    runGame("tri");
  } else if(taskname === 'hexdev') {
    runHexDev();
  } else if(taskname === 'tridev') {
    runTriDev();
  } else if(taskname === 'actorstudio') {
    runActorStudio();
  } else if(taskname === 'edit') {
    editTerrainWeights();
  } else if(taskname === 'play') {
    playGame();
  }

}


